@import url(https://fonts.googleapis.com/css?family=Montserrat:100,200,300,400,500,700);
.footer {
  background-color: #dcdce1;
  color: #000000;
}

.footer > .container {
  padding-right: 15px;
  padding-left: 15px;
}

html,
body {
  height: 100%;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
}

.page-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.content-wrap {
  flex: 1 1;
}

.anchor-black {
  text-decoration: none;
  color: black;
}

.heading {
  text-align: center;
  margin-top: 3rem;
  margin-bottom: 3rem;
}

